import { useBlockProps } from '@wordpress/block-editor';
import { RawHTML } from '@wordpress/element';

const Save = ({ attributes }) => {
    const {
        captchaSiteKey,
        currentLang,
        title,
        subtitle,
        selectLabel,
        numberLabel,
        numberPlaceholder,
        emailPlaceholder,
        emailLabel,
        checkboxLabel,
        checkboxSondageLabel,
        disclaimer,
        textTelecharge,
        telecharge,
        options
    } = attributes;

    return (
        <div {...useBlockProps.save()}>
            <div className="overlay" id="popinOverlayAutre">
                <div className="popin-jeunesse">
                    <button className="close-button" id="closePopinButtonAutre">&times;</button>
                    <div className="h1-title"><RawHTML>{title}</RawHTML></div>
                    <div className="h2-title"><RawHTML>{subtitle}</RawHTML></div>
                    <form id="form-jeunesse-autre" method="POST">
                        <div className="input-group">
                            <label>
                            <RawHTML>{selectLabel}</RawHTML><span class="required">*</span>
                            </label>
                            <select id="selectJeunesseAutre" name="statut"><span class="required">*</span>
                                {options.map((option, index) => (
                                    <option key={index} value={option}>
                                        {option}
                                    </option>
                                ))}
                            </select>
                        </div>
                        <div className="input-group">
                            <label><RawHTML>{numberLabel}</RawHTML><span class="required">*</span>
                            </label>
                            <input
                                type="number"
                                id="numberEnfantAutre"
                                value=""
                                placeholder={numberPlaceholder}
                                name="nombre_enfant"
                                min="0"
                            />
                        </div>
                        <label className="input-div">
                            <input
                                type="checkbox"
                                name="receive_news"
                                id="receiveNewsAutre"
                                value="true"
                            />
                            <span><RawHTML>{checkboxLabel}</RawHTML></span>
                        </label>
                        <label className="input-div">
                            <input
                                type="checkbox"
                                name="refuse_surveys"
                                id="refuseSurveysAutre"
                                value="true"
                            />
                            <span><RawHTML>{checkboxSondageLabel}</RawHTML></span>
                        </label>
                        <div className="show-email">
                            <p className="font-small"><RawHTML>{disclaimer}</RawHTML></p>
                            <div className="input-group">
                                <label><RawHTML>{emailLabel}</RawHTML><span class="required">*</span></label>
                                <input 
                                    type="email"
                                    id="emailAutre"
                                    placeholder={emailPlaceholder}
                                    name="email" />
                            </div>
                        </div>
                        <input type="hidden" name="type" value="PDF"/>
                        <div
                            className="frc-captcha"
                            data-lang={currentLang === 'fr' ? 'fr' : 'en'}
                            data-sitekey={captchaSiteKey}
                        ></div>

                        <div className="popin-jeunesse--telecharger">
                            <div><RawHTML>{textTelecharge}</RawHTML></div>
                            <button
                                type="submit"
                                action="closePopinButtonAutre-submit"
                                name="submit_jeunesse_form_autre"
                                className="btn-with-svg btn-with-svg--download"
                            >
                                {telecharge}
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
};

export default Save;